



































































































































import { Vue, Prop, Component, Watch, Inject } from "vue-property-decorator";
import { mixins } from "vue-class-component";
import { submitWork } from "@/services/api/submission";
import { getProofread, saveProofRead } from "@/services/api/proofreads";
import LoaderMixin from "@/mixins/loader";
import NotificationMixin from "@/mixins/notification";

@Component
export default class ProofcheckForm extends mixins(
  LoaderMixin,
  NotificationMixin
) {
  @Prop({ type: Boolean, default: false }) private showDialog: boolean;
  @Prop(Number) private submissionId: number;
  @Prop(Number) private submissionUserId: number;
  private dialog: boolean = false;
  private valid: boolean = false;
  private title: string = "";
  private mensagemOrganizacao = "";
  private items = {
    workOriginalite: {
      valueSelected: 0,
      title: "Originalidade do trabalho: ",
      subtitle:
        "Como voc avalia a originalidade do trabalho apresentado nesse artigo?",
      options: [
        { value: 5, text: "5. Muito original" },
        { value: 4, text: "4. Originalidade acima da mdia" },
        { value: 3, text: "3. Originalidade mdia" },
        { value: 2, text: "2. Pouca originalidade" },
        { value: 1, text: "1. Nenhuma originalidade" }
      ]
    },
    areaContribution: {
      valueSelected: 0,
      title: "Contribuio para a rea: ",
      subtitle: "Como voc avalia a contribuio do artigo para a rea?",
      options: [
        { value: 5, text: "5. Grande contribuio para a rea" },
        { value: 4, text: "4. Contribuio significante" },
        { value: 3, text: "3. Contribuio pequena, mas clara" },
        { value: 2, text: "2. Pouca contribuio" },
        {
          value: 1,
          text: "1. Nenhuma contribuio discernvel para a rea"
        }
      ]
    },
    technicalQuality: {
      valueSelected: 0,
      title: "Qualidade tcnica: ",
      subtitle:
        "Como voc avalia a qualidade tcnica do trabalho apresentado nesse artigo?",
      options: [
        { value: 5, text: "5. Excelente" },
        { value: 4, text: "4. Boa" },
        { value: 3, text: "3. Mdia" },
        { value: 2, text: "2. Baixa" },
        {
          value: 1,
          text: "1. Inaceitvel"
        }
      ]
    },
    presentationOrganization: {
      valueSelected: 0,
      title: "Organizao e apresentao: ",
      subtitle: `Como voc avalia a legibilidade, clareza e organizao desse artigo?
         As citaes e formatao esto de acordo com o estilo da conferncia?`,
      options: [
        { value: 5, text: "5. Excelente" },
        { value: 4, text: "4. Boa" },
        { value: 3, text: "3. Mdia" },
        { value: 2, text: "2. Baixa" },
        {
          value: 1,
          text: "1. Inaceitvel"
        }
      ]
    },
    recomendation: {
      valueSelected: 0,
      title: "Recomendao para esse trabalho: ",
      subtitle: "O que voc recomenda para esse artigo?",
      options: [
        {
          value: 5,
          text:
            "5. Deve ser aceito - Tenho fortes argumentos a favor da aceitao"
        },
        {
          value: 4,
          text:
            "4. Aceitao fraca - No irei lutar fortemente pela aceitao desse trabalho"
        },
        { value: 3, text: "3. Neutro - No tenho certeza" },
        {
          value: 2,
          text: "2. Rejeio fraca - No irei lutar fortemente contra"
        },
        {
          value: 1,
          text:
            "1. No deve ser aceito - Tenho fortes argumentos a favor da no aceitao"
        }
      ]
    },
    trust: {
      valueSelected: "",
      title: "Confiana do revisor: ",
      subtitle: "Qual  a sua confiana a respeito dessa reviso?",
      options: [
        { value: 3, text: "Sou um especialista na rea" },
        {
          value: 2,
          text: "Sou familiarizado com trabalho nessa rea"
        },
        { value: 1, text: "No sou um especialista na rea" }
      ]
    },
    category: {
      valueSelected: "",
      title: "Categoria: ",
      subtitle: "Em qual categoria o trabalho se enquadra?",
      options: [
        { value: "oral-presentation", text: "Apresentao Oral" },
        { value: "poster", text: "Poster" }
      ]
    }
  
  };

  constructor() {
    super();
  }

  @Watch("showDialog")
  private showDialogChanged(newValue: boolean, oldValue: boolean) {
    this.dialog = newValue;
  }

  private hideDialog() {
    this.$emit("hidden");
  }

  private async saveRevision() {
    for (let prop in this.items) {
      if (Object.prototype.hasOwnProperty.call(this.items, prop)) {
        // @ts-ignore
        if (!this.items[prop].valueSelected) {
          this.showErrorNotification("Por favor, preencha todos os campos.");
          return;
        }
      }
    }
    let data = {
      userId: this.submissionUserId,
      submissionId: this.submissionId,
      originalidade: this.items.workOriginalite.valueSelected,
      contribuicao: this.items.areaContribution.valueSelected,
      qualidade: this.items.technicalQuality.valueSelected,
      organizacao: this.items.presentationOrganization.valueSelected,
      recomendacao: this.items.recomendation.valueSelected,
      confiancaRevisor: this.items.trust.valueSelected,
      categoria: this.items.category.valueSelected,
      mensagemOrganizacao: this.mensagemOrganizacao
    };
    this.showLoader();
    saveProofRead(data)
      .then(() => {
        this.showSuccessNotification("Reviso enviada com sucesso.");
        this.hideLoader();
        this.hideDialog();
        this.$emit("loadData");
      })
      .catch(err => {
        this.hideLoader();
        console.log(err);
        this.showErrorNotification("Ocorreu um problema ao enviar reviso.");
      });
  }
}
